import React, { useState, useRef } from 'react';
import Notification from '../notice/notice'
import Tips from '../tips/tips'
import Top from '../top/top'
import './index.scss';
import { CSSTransition } from 'react-transition-group';

function Left() {
    const [ hide, SetHide ] = useState(true)
    const nodeRef = useRef(null);
    const onHide = ()=>{
        SetHide(!hide)
    }
    return <div id="left-container">
        {/* https://stackoverflow.com/questions/62187461/using-react-finddomnode-is-deprecated-in-strictmode-is-thrown-as-a-warning-when */}
        <CSSTransition nodeRef={nodeRef} in={hide} timeout={300} appear  classNames="fade">
            <div id="left-box" ref={nodeRef}>
                <Top></Top>
                <Notification></Notification>
                <Tips></Tips>
            </div>
        </CSSTransition>
        <div id="hide" onClick={onHide}>{ hide ? '〉' : '〈' }</div>
    </div>
}
export default Left;