function DayItem(props) {
    const showFestival = (data) => {
        // const solarFestival = data.solarFestival && data.solarFestival.split(' ')[0]
        return data.solarFestival || data.lunarFestival || data.lunarDayName
    }
    let timer = null
    const keyUp = (e)=>{
        clearTimeout(timer)
        timer = setTimeout(()=>{
            props.change(props.data.context, e.target.value)
        }, 400)
    }
    const textBottom = showFestival(props.data)
    return <div className={`item ${props.data.type !== 1 ? 'un-item' : ''} ${props.data.context === props.currentDate ? 'current-date' : ''}`}>
        <div className="item-top">
            <span>{props.data.day}</span>
            <textarea onKeyUp={keyUp} defaultValue={props.tipData}></textarea>
        </div>
        <div className="item-bottom sing-hidden-font" title={textBottom}>{textBottom}</div>
    </div>
    // return <div className={`item ${props.data.type !== 1 ? 'un-item' : ''} ${props.data.context === props.currentDate ? 'current-date' : ''}`}>
    //     <textarea onKeyUp={keyUp} defaultValue={props.tipData}></textarea>
    //     <div class="h1">{props.data.day}</div>
    //     <div class="h2">{textBottom}</div>

    // </div>
}

export default DayItem;