/**
 * @desc: 
 * @param {*}
 * @return {*}
 * 用于管理本地loaction
 * 保存日历信息
 */
const DATE_MAP_NAME = 'dateMap';
class state {
    constructor() {
        this.dateMap = {}
        this.init()
    }
    init () {
        this.dateMap = this.getLocation()
    }
    change (name, val) {
        this.dateMap[name] = val
        if(val === '' || val === null || val === undefined) {
            delete this.dateMap[name]
        }
        this.saveLocation()
    }
    getData () {
        return {...this.dateMap}
    }
    saveLocation() {
        localStorage.setItem(DATE_MAP_NAME, JSON.stringify(this.dateMap))
    }
    getLocation () {
        return JSON.parse(localStorage.getItem(DATE_MAP_NAME)) || {}
    }
}
const dateState = new state()
export default dateState;
