import { Modal, Input, Radio,TimePicker  } from 'antd';
import React, { useState } from 'react';
import moment from 'moment';
const timeFontList = {
    1:'每这个超过这个时间提醒一次',
    2:'每天这个时候提醒',
    3:'工作日这个时候提醒',
    4:'这个时候提醒',
}
function AddNotice(props) {
    // const [] = useState()
    const defTime = moment('00:00', 'HH:mm')
    const [type, setType] = useState(1);
    const [title, setTitle] = useState('');
    const [context, setContext] = useState('');
    const [time, setTime] = useState(defTime);

    const timeChange = (time, timeString) =>{
        console.log(time)
        setTime(timeString);
    }
    const radioChange = (e) =>{
        console.log('radio checked', e.target.value);
        setType(e.target.value);
    }
    const ok = ()=>{
        props.onOk({
            type,
            title,
            context,
            time,
            id: new Date().getTime(0)
        })
    }
    const cancel = ()=>{
        setType(1)
        setTitle('')
        setContext('')
        setTime(moment('00:00:00', 'HH:mm:ss'))
        props.onCancel()
    }
    return <Modal title="新增提示" visible={props.visible} onOk={ok} onCancel={cancel} okText="确定"  cancelText="取消">
    <div>标题:<Input placeholder="请输入标题" value={title} onChange={ e=> setTitle(e.target.value) }></Input></div>
    <div>内容:<Input placeholder="请输入内容" value={context} onChange={ e=> setContext(e.target.value) }></Input></div>
    <div>
        <p>类型：</p>
        <div>
        <Radio.Group onChange={radioChange} value={type}>
            <Radio value={1}>定时</Radio>
            {/* <Radio value={2}>每天</Radio>
            <Radio value={3}>工作日</Radio>
            <Radio value={4}>只一次</Radio> */}
        </Radio.Group> 
        </div>
    </div>
    <div>{timeFontList[type]}： 
         <TimePicker onChange={timeChange} defaultOpenValue={defTime} format='HH:mm' />
    </div>
</Modal>
}
export default AddNotice;