import React, { useEffect, useState } from 'react';

import './index.scss';

function Tips() {
    const [ value, setValue ] = useState('')
    const change = (e) =>{
        localStorage.setItem('tipsDate', e.target.value)
        setValue(e.target.value)
    }
    useEffect(()=>{
        setValue(localStorage.getItem('tipsDate') || null)
    }, [])
    return <div id="tips-container">
        <p id="tips-title">便利签</p>
        <textarea id="tips-textarea" placeholder="请输入" defaultValue={value} onChange={change}></textarea>
    </div>
}
export default Tips;