/**
 * 日历方法
 * 提示
 * 月份是0-11
 */
import { FN_TYPE, MONTH } from '../constant/index';


 // 1.为了获得每个月的日期有多少，我们需要判断 平年闰年[四年一闰，百年不闰，四百年再闰]
 const isLeapYear = (year) => {
    return (year % 400 === 0) || (year % 100 !== 0 && year % 4 === 0);
  };

  // 2.获得每个月的日期有多少，注意 month - [0-11]
  const getMonthCount = (year, month) => {
    let arr = [
      31, null, 31, 30, 
      31, 30, 31, 31,
      30, 31, 30, 31
    ];
    let count = arr[month] || (isLeapYear(year) ? 29 : 28);
    return Array.from(new Array(count), (item, value) => value + 1);
  };

  // 3.获得某年某月的 1号 是星期几，这里要注意的是 JS 的 API-getDay() 是从 [日-六](0-6)，返回 number
  const getWeekday = (year, month) => {
    let date = new Date(year, month, 1);
    return date.getDay();
  };

// 4.获得上个月的天数
const getPreMonthCount = (year, month) => {
    if (month === 0) {
        return getMonthCount(year - 1, MONTH.MAX);
    } else {
        return getMonthCount(year, month - 1);
    }
};
// 5.获得下个月的天数
const getNextMonthCount = (year, month) => {
    if (month === MONTH.MAX) {
        return getMonthCount(year + 1, MONTH.MIN);
    } else {
        return getMonthCount(year, month + 1);
    }
};

// 6. 加减月份 返回月份和年份
const changeMonth = (type, year, month) =>{// cut or add
  if(type === FN_TYPE.CUT) {
    const i = month === MONTH.MIN
    return {
      month: i ? MONTH.MAX : month-1,
      year: i ? year-1 : year
    }
  } else if(type === FN_TYPE.ADD)  {
    const i = month === MONTH.MAX
    return {
      month: i ? MONTH.MIN : month+1,
      year: i ? year+1 : year
    }
  }
}

const getWeekMap = ['日','一','二','三','四','五','六'];

export { isLeapYear, getMonthCount, getWeekday, getPreMonthCount, getNextMonthCount, getWeekMap, changeMonth }