class notice {
    constructor(option) {
        this.enable = false; //是否拥有权限 
        this.support = true; // 浏览器是否支持
        this.list = []; // 消息队列
        this.option = Object.assign({
            warn: '请点击允许通知！！！',
            loopTime: 1000*60,// 一分钟
            title: '提示',
            body: '内容',
            // icon :'https://avatars.githubusercontent.com/u/20680344?s=460&u=00d3aa99c843c5bef6d8172bf433b21e23acc950&v=4',
        }, option); // 初始化信息
        this.id = 0;
        this.timer = null;
        this.init()
    }
    init () {
        if (window.Notification) {
            if (Notification.permission === "granted") {
                this.enable = true
            } else if (Notification.permission !== "denied") {
                Notification.requestPermission(function (permission) {
                    if (permission === "granted") {
                        this.enable = true
                    }
                });
            }
        } else {
            this.support = false
        }
    }
    canRun () {
        if(!this.support) return false
        if(this.enable) return true
        this.init()
        return false 
    }
    tip (opt, index, callback) {
        var ev = new Notification((opt.title || this.option.title), {
            body: opt.body || this.option.body,
            icon: this.option.icon,
            timestamp: 1000,
            tag: opt.id,
        });
        console.log(ev)
        ev.onshow = () =>{
            console.log('通知显示了！', index);
        }
        ev.onerror =  (err) => {
            console.log('出错了，小伙子在检查一下吧', err);
        }
        ev.onclick = () => {
            callback && callback();
            ev.close();
            window.focus();
            this.remove(index)
        };
    }
    loop () {
        if(!this.canRun()) {
            alert(this.option.warn)
            return false
        }
        if(this.list.length === 0)  {
            return false
        }
        else {
            clearInterval(this.timer)
        }
        this.timer = setInterval(() => {
            for(let index = 0, len = this.list.length; index < len; index++) {
                if(this.list[index].time <= new Date().getTime()) {
                    this.action(this.list[index], index)
                }  
            } 
        }, this.option.loopTime);
    }
    action (item, index) {
        this.tip(item, index, item.callback)
    }
    add (obj) {
        this.list.push({
            id: this.id++,
            ...obj
        })
        this.loop()
    }
    remove (index) {
        this.list.splice(index, 1)
        if(this.list.length === 0) clearInterval(this.timer)
    }
    clear () {
        this.list = []
        clearInterval(this.timer)
    }
    
}
export default notice;