import React, { useEffect, useState } from 'react';
import notice from '../../unit/notice';
import AddNotice from './addNotice';
import './index.scss';
import { Switch,Button   } from 'antd';
import { DeleteOutlined } from '@ant-design/icons';

// import {  } from 'antd';
// https://ant.design/components/time-picker-cn/

const icon = 'https://avatar-static.segmentfault.com/371/373/3713730051-584116ef272ee_big64'//require('../../images/1.jpg')
const fnNotice = new notice({icon})



function Notice() {
    // 音乐提示
    // 隐藏这个区域
    // const [water, setWater] = useState(false);
    // const [wc, setWc] = useState(true);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = (obj) => {
        setIsModalVisible(false);
        // add list 
        const arr = [...taskList, obj]
        setTaskList(arr)
         // add notice
         const t = obj.time.split(":")
         const no = {
            title: obj.title,
            body:obj.context,
            time: (t[0] * 60 * 60 * 1000) + (t[1] * 60 * 1000)
        }
        fnNotice.add(no)
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };
    // 上面都是model
    const [taskList, setTaskList] = useState([]);
    useEffect(()=>{
        fnNotice.init()
    },[])
    // const submit = () =>{
        // const objWater = {
        //     title: '提示',
        //     body:'该喝水了，一天八杯水，水是万能的',
        //     time: 1000*30,
        // }
        // const objWc = {
        //     title: '提示',
        //     body:'去个厕所，到处走走，久坐不健康',
        //     time: 1000*30,
        // }
       
        // water && fnNotice.add(objWater)
        // wc && fnNotice.add(objWc)
    // } 
    const del = (index) =>{
        console.log(index)
        let arr = [...taskList]
        arr.splice(index,1);
        setTaskList(arr)
    }
    const changeTip = (checked) =>{
        if(!checked) {
            fnNotice.clear()
        } else {
            
        }
    }
    return <div className="notice-container">
        <p className="notice-title">提醒&nbsp;<Switch defaultChecked onChange={changeTip} /></p>
        <div>
            {
                taskList.map((item, index) =>{
                return <div className="notice-item" key={item.id}><span>{item.title}-{item.context}-{item.time}</span><Button icon={<DeleteOutlined />} shape="circle" onClick={()=> del(index)}   type="primary" size=""></Button></div>
                })
            }
        </div>
        <Button onClick={showModal}  type="primary">新增</Button>
        <AddNotice visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}></AddNotice>
    </div>
}
export default Notice;